.home-cursor {
  display: inline-block;
  width: 10px;
  height: 1rem;
  margin-left: 5px;
  animation-timeline: auto;
  animation-range-start: normal;
  animation-range-end: normal;
  background: rgb(254, 81, 134);
  border-radius: 1px;
  animation: 1s ease 0s infinite normal none running cursor;
}
.logo {
  display: flex
;
  align-items: center;
  font-weight: bold;
  font-family: monospace, monospace;
  text-decoration: none;
}
@keyframes cursor {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}

.home {
  .home-profile {
    @include transform(translateY( 16vh));
    padding: 0 0 .5rem;
    text-align: center;

    .home-avatar {
      padding: .5rem;

      img {
        display: inline-block;
        width: 12rem;
        height: 12rem;
        margin: 0 auto;
        @include border-radius(100%);
        @include box-shadow(0 0 0 .3618em rgba(0, 0, 0, .05));
        @include transition(all 0.4s ease);

        &:hover {
          position: relative;
          @include transform(translateY(-.75rem));
        }
      }
    }

    .home-title {
      font-size: 1.6rem;
      font-weight: bold;
      margin: 0;
      padding: .5rem;
      [theme=dark] & {
        color: $global-font-title-color-dark;
      }
    }

    .home-subtitle {
      font-size: 1.4rem;
      font-weight: normal;
      margin: 0;
      padding: .5rem;

     
    }

    .links {
      padding: .5rem;
      font-size: 1.5rem;

      a * {
        vertical-align: text-bottom;
      }

      img {
        height: 1.5rem;
        padding: 0 .25rem;
      }
    }

    .links a{
      [theme=dark] & {
        color: $global-links-color-dark;
      }
      &:hover {
        color:$single-link-hover-color-dark
      }
    }	  

    .home-disclaimer {
      font-size: 1.3rem;
      line-height: 1.7rem;
      font-weight: 100;
      margin: 0;
      padding: .5rem;
      color: $global-font-secondary-color;

      [theme=dark] & {
        color: $global-font-disclaimer-color-dark;
      }
    }
  }
}

.home[data-home=posts] {
  .home-profile {
    @include transform(translateY(0));
    padding-top: 2rem;
  }

  .home-avatar img {
    width: 12rem;
  }

  .summary {
    padding-top: 1rem;
    padding-bottom: .8rem;
    color: $global-font-color;
    border-bottom: 1px dashed $global-border-color;

    [theme=dark] & {
      color: $global-font-color-dark;
      border-bottom: 1px dashed $global-border-color-dark;
    }

    .featured-image-preview {
      width: 100%;
      padding: 30% 0 0;
      position: relative;
      margin: 0.6rem auto;
      @include transition(transform 0.4s ease);

      img {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;

        &.lazyloaded {
          @include object-fit(cover);
        }
      }

      &:hover {
        @include transform(scale(1.01));
      }
    }

    .single-title {
      font-size: 1.25rem;
      line-height: 140%;
      margin: 0.4rem 0;
    }

    

    .content {
      @include box(vertical);
      -webkit-line-clamp: 3;
      margin-top: .3rem;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      @include overflow-wrap(break-word);
      color: $global-font-secondary-color;

      [theme=dark] & {
        color: $global-font-secondary-color-dark;
      }

      h2,
      h3,
      h4,
      h5,
      h6,
      p {
        font-size: 1rem;
        line-height: 1.5;
        display: inline;

        &::after {
          content: "\A";
          white-space: pre;
        }
      }

      h2 {
        font-size: 1.125rem;
      }

      @include link(false, true);

      b, strong {
        color: $global-font-secondary-color;

        [theme=dark] & {
          color: $global-font-secondary-color-dark;
        }
      }
    }

    .post-footer {
      margin-top: .4rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: .875rem;
      
      @include link(false, false);

      .post-tags {
        padding: 0;

        a {
          color: $single-link-color;
          &:hover {
            color: $single-link-hover-color;
          }
        }	
      }
    }
  }
}
